export function getBubbleSortAnimations(array){
    const animations = [];
    if (array.length <= 1) return array;
    bubbleSortHelper(array, array.length - 1, animations);
    return animations;
}
function bubbleSortHelper(
    mainArray,
    endIdx,
    animations,
) {
    let boo = true
    const eidx = endIdx;
    for (let i = 0; i < eidx; ++i){
        boo = false
        for(let j = 0; j < eidx-i; ++j){
            animations.push([j, j+1,0,mainArray[j],mainArray[j+1]]);
            if(mainArray[j] > mainArray[j+1]){
                animations.push([j, j+1,1,mainArray[j],mainArray[j+1]]);
                sweap(mainArray, j, j + 1);
                boo = true
            }
            else{
                animations.push([j, j+1,1,mainArray[j+1],mainArray[j]]);
            }
        }
        
        if (boo === false) {
            break;
        }
    }

}
function sweap(
    mainArray,
    idx1,
    idx2
){
    let temp = mainArray[idx1];
    mainArray[idx1] = mainArray[idx2];
    mainArray[idx2] = temp;
}