
import React, { useState } from 'react';




export const Slide = ({resetArray,min,max}) => {
  // console.log("This is slide component", resetArray.);
  const slideChange = () =>{
    setValue(document.getElementById("myslide").value);
    resetArray();
    // resetArray(document.getElementById("myslide").value);
  }
  const [value,setValue] = useState(10);
  return (
    <>

    <input type="range" value={value} min={min} max={max} onChange={slideChange} id="myslide"></input>
    </>
  );
}


export const SlideSpeed = ({fun,min,max}) => {
  const slideChange = () =>{
    setValue(document.getElementById("speedslide").value);
    fun();
    // resetArray(document.getElementById("myslide").value);
  }
  const [value,setValue] = useState(10);
  return (
    <>

    <input type="range" value={value} min={min} max={max} onChange={slideChange} id="speedslide"></input>
    </>
  );
}