import React from 'react';
import './home.css';
// import {getMergeSortAnimations} from './getMergeSortAnimation'

import {getBubbleSortAnimations} from './getBubbleSortAnimations'
import {getQuickSortAnimations} from './gerQuickSortAnimations'

import {merge} from './getMertgeSortAnimation';

import {Slide, SlideSpeed} from './slide'




// Change this value for the speed of the animations.


// Change this value for the number of bars (value) in the array.
//const NUMBER_OF_ARRAY_BARS = 310;

// This is the main color of the array bars.
const PRIMARY_COLOR = 'Blue';

// This is the color of array bars that are being compared throughout the animations.
const SECONDARY_COLOR = 'yellow';
const PIOVOT_COLOR = 'red';

export class Home extends React.Component{
    constructor(props){
        super(props);
        this.state = {
          array: [10, 12],
          ANIMATION_SPEED_MS: 10,
        };
    }

    componentDidMount(){

        this.resetArray(10);
    }

  startSort = () => {
    const algo = document.querySelector("#select");



    if (algo.value === "Merge Sort") {
      this.mergeSort();
    }
    if (algo.value === "Bubble Sort") {
      this.bubbleSort();
    }
    if (algo.value === "Quick Sort") {
      this.quickSort();
    }
  }
  animationSpeed(speed) {
    this.setState({ANIMATION_SPEED_MS : speed})
  }
  speed = () => {
    let n = document.getElementById("speedslide").value;
          this.animationSpeed(n);
  }
resetArray(ref){
  let n = ref;
    const array = [];

    for(let i = 0; i < n; ++i){
        array.push(randomIntFromInterval(10,300));
    }
    this.setState({array});
}

  
reset = () =>{
          let n = document.getElementById("myslide").value;
          this.resetArray(n);
}


heapSort(){

}
mergeSort(){
  const ANIMATION_SPEED_MS = 100/this.state.ANIMATION_SPEED_MS; 
  const m = document.getElementsByTagName("button");
  let timeout = 0;
  for(let i = 0 ; i < m.length; ++i){
    m[i].disabled = true;
  }
  const s = document.getElementById("myslide");
  s.disabled = true;
  const [CHANGE] = merge(this.state.array);
  
  
  const arrBar = document.getElementsByClassName("array-bar");
  
  for(let i = 0; i < CHANGE.length; i++){
    let current = CHANGE[i];
    
    
    if(current[1] === 0){
      
      setTimeout(()=>{
        arrBar[current[0]].style.backgroundColor = PRIMARY_COLOR;
      },i*ANIMATION_SPEED_MS);
    }
    else if(current[1] === 1){
      
      setTimeout(()=>{
        arrBar[current[0]].style.backgroundColor = SECONDARY_COLOR;
      },i*ANIMATION_SPEED_MS);
    }
    else if(current[1] === 2){
      
      setTimeout(()=>{
        arrBar[current[0]].style.height = `${current[2]}px`;
      },i*ANIMATION_SPEED_MS);
    }
    timeout = i;
  }
  
  
  setTimeout(()=>{
    for(let i = 0 ; i < m.length; ++i){
      m[i].disabled = false;
    }
    s.disabled = false;
  }, timeout * ANIMATION_SPEED_MS);
  
  // setTimeout(() => {
    //   this.setState({ array });
    // }, timeout * ANIMATION_SPEED_MS);
    // this.setState({array});
    // const animations = getMergeSortAnimations(this.state.array);
    //     for (let i = 0; i < animations.length; i++) {
      //       const arrayBars = document.getElementsByClassName('array-bar'); 
      //       const isColorChange = i % 3 !== 2;
      //       if (isColorChange) {
        //         const [barOneIdx, barTwoIdx] = animations[i];
//         const barOneStyle = arrayBars[barOneIdx].style;
//         const barTwoStyle = arrayBars[barTwoIdx].style;
//         const color = i % 3 === 0 ? SECONDARY_COLOR : PRIMARY_COLOR;
//         //console.log(i);
//         setTimeout(() => {
  //           // console.log("changig color");
  //           barOneStyle.backgroundColor = color;
  //           barTwoStyle.backgroundColor = color;
//         }, i * ANIMATION_SPEED_MS);
//       } else {
  //         setTimeout(() => {
    //           //console.log("Cahnging height");
    //           const [barOneIdx, newHeight] = animations[i];
    //           const barOneStyle = arrayBars[barOneIdx].style;
    //           barOneStyle.height = `${newHeight}px`;
    //         }, i * ANIMATION_SPEED_MS);
    //       }
    //       timeout = i;
    //     }
    
    //     setTimeout(()=>{
      //       for(let i = 0 ; i < m.length; ++i){
        //         m[i].disabled = false;
        //       }
        //       s.disabled = false;
        //     }, timeout * ANIMATION_SPEED_MS);
      }
      
      bubbleSort(){
        const ANIMATION_SPEED_MS = 100/this.state.ANIMATION_SPEED_MS; 
        const m = document.getElementsByTagName("button");
        const s = document.getElementById("myslide");
        for(let i = 0 ; i < m.length; ++i){
          m[i].disabled = true;
        }
        s.disabled = true;
        let timeout = 0;
        const animations = getBubbleSortAnimations(this.state.array);
        const arrayLength = animations.length;
        for(let i = 0; i < arrayLength; ++i){
          const [barOneIdx,barTwoIdx,col,barOneHeight,barTwoHeight] = animations[i];
          const arrayBars = document.getElementsByClassName('array-bar');
          const barOne = arrayBars[barOneIdx].style;
          const barTwo = arrayBars[barTwoIdx].style;
          if(col === 0){
            setTimeout(() =>{
              barOne.backgroundColor = SECONDARY_COLOR;
              barTwo.backgroundColor = SECONDARY_COLOR;
      }, i * ANIMATION_SPEED_MS);
      clearTimeout();
    }
    else{
      setTimeout(() => {
        barOne.backgroundColor = PRIMARY_COLOR;
        barTwo.backgroundColor = PRIMARY_COLOR;
        barOne.height = `${barTwoHeight}px`;
        barTwo.height = `${barOneHeight}px`;
      }, i * ANIMATION_SPEED_MS);
    }
    timeout  = i;
  }
  
  
  setTimeout(() => {
    for(let i = 0 ; i < m.length; ++i){
      m[i].disabled = false;
    }
    s.disabled = false;
  },timeout*ANIMATION_SPEED_MS);
}


quickSort() {
  const ANIMATION_SPEED_MS = 100/this.state.ANIMATION_SPEED_MS; 
  // console.log(this.state.array)
  // console.log(getQuickSortAnimations(this.state.array))
    const m = document.getElementsByTagName("button");
  const s = document.getElementById("myslide");
  for(let i = 0 ; i < m.length; ++i){
    m[i].disabled = true;
  }
  s.disabled = true;
  let timeout = 0;

    const animations = getQuickSortAnimations(this.state.array);

    const length = animations.length;

    for (let i = 0; i < length; i++){
      const animation = animations[i];

      if (animation.length === 3) {
        if (animation[animation.length - 1] === 4) {
          setTimeout(() => {
            
            const index = animation[0];
            const arrayBars = document.getElementsByClassName('array-bar');
            const bar = arrayBars[index].style;
            bar.backgroundColor = PIOVOT_COLOR;
            // console.log(index)
          },i*ANIMATION_SPEED_MS)
        }

        if (animation[animation.length - 1] === 0) {
          setTimeout(() => {
            
            const index1 = animation[0];
            const index2 = animation[1];
            const arrayBars = document.getElementsByClassName('array-bar');
            const barOne = arrayBars[index1].style;
            const barTwo = arrayBars[index2].style;
            
            barOne.backgroundColor = PRIMARY_COLOR;
            barTwo.backgroundColor = PRIMARY_COLOR;
          },i*ANIMATION_SPEED_MS)
        }

        if (animation[animation.length - 1] === 2) {
          setTimeout(() => {
            
            const index1 = animation[0];
            const index2 = animation[1];
            const arrayBars = document.getElementsByClassName('array-bar');
            const barOne = arrayBars[index1].style;
            const barTwo = arrayBars[index2].style;
            
            barOne.backgroundColor = SECONDARY_COLOR;
            barTwo.backgroundColor = SECONDARY_COLOR;
          },i*ANIMATION_SPEED_MS)
          }
      }
      else {
        setTimeout(() => {
            
          const [index1, height1, index2, height2] = animation;
          const arrayBars = document.getElementsByClassName('array-bar');
          const barOne = arrayBars[index1].style;
          const barTwo = arrayBars[index2].style;
          
          barOne.height = `${height1}px`;
          barTwo.height = `${height2}px`;
        },i*ANIMATION_SPEED_MS)
      }
      timeout  = i;
    }
setTimeout(() => {
    for(let i = 0 ; i < m.length; ++i){
      m[i].disabled = false;
    }
    s.disabled = false;
  },timeout*ANIMATION_SPEED_MS);

  }


    render(){
      const array = this.state.array;  
      const wi = (1 / array.length) * 700;
        return(
            <div className="array-container">
           {array.map((value,idx) => (
           <div className="array-bar"  key={idx}
        style={
            {
                backgroundColor: PRIMARY_COLOR,height: `${value}px`, width: `${wi}px`,
                
            }
        }>
              
           </div>
           ))}
           <br/>
           
           
            
            <div className="container">
              <button onClick={() => this.reset()}>Generate New Array</button>
              <div className="algo-container">
                <select defaultValue="Merge Sort" id="select">
                  <option value="Merge Sort">Merge Sort</option>
                  <option value="Bubble Sort">Bubble Sort</option>
                  <option value="Quick Sort">Quick Sort</option>
                </select>
              </div>
              <div className="slide-bar">
                <Slide resetArray={this.reset} min="10" max={window.innerWidth/6}></Slide>
                <p htmlFor="slide">Array Size</p>
              </div>
              <div className="slide-bar">
                <SlideSpeed fun={this.speed} min="1" max="100"></SlideSpeed>
                <p htmlFor="slide">Array Size</p>
              </div>

              <button className="run-algo" onClick={this.startSort}>Sort</button>
            </div>
            
            </div>
        );
    }
};
// From https://stackoverflow.com/questions/4959975/generate-random-number-between-two-numbers-in-javascript
function randomIntFromInterval(min, max) {
    // min and max included
    return Math.floor(Math.random() * (max - min + 1) + min);
  }
  