

export function getQuickSortAnimations(array) {
    const animations = []

    quickSort(array, 0, array.length-1, animations);
    return animations;
}


function quickSort(array, min, max, animations) {
    if (min < max) {
        
        const pi = getPivot(array, min, max, animations);
        
        quickSort(array, min, pi - 1, animations);
        quickSort(array, pi+1,max, animations);
    }
}


function getPivot(array, min, max, animations) {
    let pivot = array[max];
    let i = (min - 1);
    animations.push([max, 0, 4]);
    for (let j = min; j < max; j++){
        animations.push([max, j, 2]);
        if (array[j] < pivot) {
            i++;
            animations.push([i, array[i], j, array[j]]);
            [array[i], array[j]] = [array[j], array[i]];
            animations.push([i, array[i], j, array[j]]);
            animations.push([i, j, 0]);
        }
        animations.push([max, j, 0]);
    }
    i++;
    animations.push([i, array[i], max, array[max]]);
    [array[i], array[max]] = [array[max], array[i]];
    animations.push([i, array[i], max, array[max]]);
    animations.push([i, max, 0]);
    return i;
}