function mergeSort(arr,min,max){
    if(min >= max){
        return;
    }

    // Calculating mid range to divide array in two part 
    const mid = Math.floor((min+max)/2);

    // recursive call left half
    mergeSort(arr,min,mid);

    // recursive call for right half
    mergeSort(arr,mid+1,max);

    // merging sub arrays
    doMerge(arr,min,mid,max);
}

function doMerge(arr,min,mid,max){

    let r = [];
    let l = [];

    const n1 = mid-min+1;
    const n2 = max-mid;
    for(let i = 0; i < n1; i++){
        l.push(arr[min+i]);
    }
    for(let i = 0; i < n2; i++){
        r.push(arr[mid+1+i]);
    }
    let i = 0;
    let j = 0;
    let k = min;


    // Compair and copy the element in main array in sorted form

    while(i<n1 && j < n2){
        //TODO: add in a array

        CHANGE.push([min+i,1]);
        CHANGE.push([mid+j+1,1]);
        if (l[i] <= r[j]) {

            arr[k] = l[i];
            //TODO: add in a array

            CHANGE.push([k,1]);
            CHANGE.push([k,2,l[i]]);
            CHANGE.push([k,0]);
            CHANGE.push([min+i,0]);
            i++;
        }
        else {
            arr[k] = r[j];
     
            //TODO: add in a array
            CHANGE.push([k,1]);
            CHANGE.push([k,2,r[j]]);
            CHANGE.push([k,0]);
            CHANGE.push([mid+j+1,0]);
            j++;
        }
        k++;
        
        
    }
        // Copy the remaining elements of
    // L[], if there are any
    while (i < n1) {
        arr[k] = l[i];
       
        //TODO: add in a array
        CHANGE.push([k,1]);
            CHANGE.push([k,2,l[i]]);
            CHANGE.push([k,0]);
            CHANGE.push([min+i,0]);
        i++;
        k++;
    }
 
    // Copy the remaining elements of
    // R[], if there are any
    while (j < n2) {
        arr[k] = r[j];
  
        //TODO: add in a array
        CHANGE.push([k,1]);
            CHANGE.push([k,2,r[j]]);
            CHANGE.push([k,0]);
            CHANGE.push([mid+j+1,0]);
        j++;
        k++;
    }
    

 


}